import React from 'react';
import { RenderNodeProps, Controller } from '@ali/4ever-cangjie';
import { TemplateButtonConfig } from '../type';
import { TemplateButton } from '../mo';
import CloseView from './closeView';
import OpenView from './openView';

interface ContainerProps extends RenderNodeProps<TemplateButton> {
  config?: TemplateButtonConfig;
  editable?: boolean;
  isSelected: boolean;
  controller: Controller;
  child: () => React.ReactNode;
}

const Container: React.FC<ContainerProps> = (props) => {
  const {
    node,
    controller,
    child,
    config,
    isSelected,
    editable = true,
  } = props;
  const { data: { metadata } } = node;
  const placeholder = config?.locale?.namePlaceholder || 'Add a new item';
  if (metadata.isOpen) {
    return (
      <OpenView
        {...metadata}
        placeholder={placeholder}
        controller={controller}
        nodeKey={node.key}
        child={child}
        config={config}
        editable={editable}
        isSelected={isSelected}
      />
    );
  } else {
    return (
      <CloseView
        {...metadata}
        placeholder={placeholder}
        controller={controller}
        node={node}
        isSelected={isSelected}
        editable={editable}
        config={config}
      />
    );
  }
};

export { Container };
